<template>
  <div>
    <div class="mainViewCell">
      <div class="mainViewCell-leftBox">
        <!-- ------------------------------- -->
        <div :class="isCollapse?'MenuBtn transform-arrow':'MenuBtn'" @click="MenuBtnClick"></div>
        <el-menu
          :default-active="firstPage"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          @select="selectFun"
          :collapse="isCollapse"
          :unique-opened="true"
          style="background-color:RGB(24,79,168)"
        >
          <menus v-for="(model, index) in MenuArr" :model="model" :key="index"></menus>
        </el-menu>
        <!-- ---------------------------------->
      </div>
      <!-- =========================第二视口 -->
      <div class="mainViewCell-rightBox">
        <router-view name="SecondView"></router-view>
      </div>
      <!-- ========================= 第二视口-->
    </div>
  </div>
</template>
<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 250px;
  
  background-color: RGB(28, 99, 200);
}
.el-menu {
  border-right: none;
}
.el-menu-item,
.el-submenu {
  background-color: RGB(28, 99, 200);
}
.el-menu-item,
.el-submenu__title {
  color: RGB(124, 176, 253);
}

.el-menu-item.is-active {
  color: RGB(234, 251, 6);
}
/* ========== */
.el-menu-item:hover,
.el-menu-item:focus {
  background-color: #1c63c8 !important;
}
/* =========== */
</style>

<script>
import menus from "@/components/commonComp/menus";
export default {
  components: {menus},
  watch: {
    "$store.state.leftMenuBox": {
      handler(newVal) {
        this.firstPage = newVal;
        this.storage.set("leftMenuBox", newVal);
      }
    },
    "$store.state.leftMenuBoxBody": {
      handler(newVal) {
        this.MenuArr = newVal;
      },
      deep: true
    }
  },
  data() {
    return {
       firstPage: this.$store.state.leftMenuBox,
      isCollapse: false,
      // MenuArr: [
      //   {
      //     arr: [
      //       { arr: [], iName: "", url: "/riskLevelSet/1", name: "风险等级设置" },
      //       { arr: [], iName: "", url: "/rationModelSet/1", name: "定量模型设置" },
      //       { arr: [], iName: "", url: "/qutatiModelSet/1", name: "定性模型设置" },
      //     ],
      //     url: "",
      //     iName: "el-icon-location",
      //     name: "业务洗钱风险模型管理"
      //   },
      //   {
      //     arr: [
      //       { arr: [], iName: "", url: "/riskLevelSet/2", name: "风险等级设置" },
      //       { arr: [], iName: "", url: "/rationModelSet/2", name: "定量模型设置" },
      //       { arr: [], iName: "", url: "/qutatiModelSet/2", name: "定性模型设置" },
      //     ],
      //     url: "",
      //     iName: "el-icon-location",
      //     name: "产品洗钱风险模型管理"
      //   },
      //   {
      //     arr: [
      //       { arr: [], iName: "", url: "/organRiskLevelSet", name: "风险等级设置" },
      //       { arr: [], iName: "", url: "/organRationModelSet", name: "固有风险模型设置" },
      //       { arr: [], iName: "", url: "/organCtrlMeasureMod", name: "控制措施模型设置" },
      //     ],
      //     url: "",
      //     iName: "el-icon-location",
      //     name: "机构洗钱风险模型管理"
      //   },
      //   {
      //     arr: [
      //     ],
      //     url: "/evalueOrganSet",
      //     iName: "el-icon-location",
      //     name: "评估机构设置"
      //   },
      //   {
      //     arr: [   
      //       { arr: [
      //         { arr: [], iName: "", url: "/sampleDataSet", name: "样本数据集" },
      //         { arr: [], iName: "", url: "/predictDataSet", name: "预测数据集" },
      //       ], iName: "", url: "", name: "数据管理" },
      //       { arr: [], iName: "", url: "/machineLearnArithmetic", name: "机器学习算法" },
      //       { arr: [], iName: "", url: "/machineLearnModel", name: "机器学习建模" }
      //     ],
      //     url: "",
      //     iName: "el-icon-location",
      //     name: "机器学习模型管理"
      //   },
      // ]
       MenuArr:  this.$store.state.leftMenuBoxBody
    };
  },
  methods: {
    handleOpen(key, keyPath) {},
    handleClose(key, keyPath) {},
    selectFun(a) {
      this.$store.commit("leftMenuBox", {
        leftMenuBox: a
      });
    },
    MenuBtnClick() {
      this.isCollapse = !this.isCollapse;
    }
  }
};
</script>


